// src/pages/UserList.js
import React, { useState, useEffect, useMemo, useRef } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { adminUserAPI } from '../services/adminUserAPI';
import Pagination from '../components/common/Pagination';
import '../styles/pages/UserList.css';

function UserList() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const navigate = useNavigate();
  const location = useLocation();
  const isFirstLoad = useRef(true);
  
  //    
  const USERS_PER_PAGE = 12;

  //   
  const [currentPage, setCurrentPage] = useState(1);

  //    
  useEffect(() => {
    if (isFirstLoad.current) {
      //      
      const fromUserDetail = location.state?.from === 'userDetail';
      
      if (fromUserDetail) {
        //      
        const savedPage = sessionStorage.getItem('userListPage');
        if (savedPage) {
          const pageNum = parseInt(savedPage, 10);
          console.log('Restoring page from user detail:', pageNum);
          setCurrentPage(pageNum);
        }
      } else {
        //    1 
        console.log('Coming from other page, reset to page 1');
        setCurrentPage(1);
        sessionStorage.removeItem('userListPage');
      }
      
      isFirstLoad.current = false;
    }
  }, [location.state]);

  //   
  useEffect(() => {
    async function fetchUsers() {
      try {
        setLoading(true);
        const data = await adminUserAPI.getAllUsers();
        setUsers(data);
        setError(null);
      } catch (err) {
        setError('   .');
        console.error('   :', err);
      } finally {
        setLoading(false);
      }
    }

    fetchUsers();
  }, []);

  //    sessionStorage 
  useEffect(() => {
    if (!isFirstLoad.current) {
      console.log('Saving page:', currentPage);
      sessionStorage.setItem('userListPage', currentPage.toString());
    }
  }, [currentPage]);

  //  
  const filteredUsers = useMemo(() => {
    return users.filter(user => 
      user.name?.toLowerCase().includes(searchTerm.toLowerCase()) || 
      user.email?.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [users, searchTerm]);

  //     
  useEffect(() => {
    if (searchTerm) {
      setCurrentPage(1);
      sessionStorage.removeItem('userListPage');
    }
  }, [searchTerm]);

  //  
  const totalPages = Math.ceil(filteredUsers.length / USERS_PER_PAGE);
  
  //   
  const paginatedUsers = useMemo(() => {
    const startIndex = (currentPage - 1) * USERS_PER_PAGE;
    const endIndex = startIndex + USERS_PER_PAGE;
    return filteredUsers.slice(startIndex, endIndex);
  }, [filteredUsers, currentPage]);

  //   
  const handlePageChange = (newPage) => {
    console.log('Page change requested:', newPage);
    setCurrentPage(newPage);
  };

  const viewUserDetails = (userId) => {
    //       
    sessionStorage.setItem('userListPage', currentPage.toString());
    navigate(`/users/${userId}`);
  };

  if (loading) {
    return <div className="user-list-loading">   ...</div>;
  }

  if (error) {
    return <div className="user-list-error">{error}</div>;
  }

  return (
    <div className="user-list-container">
      <div className="user-list-header">
        <h1> </h1>
        <div className="search-bar">
          <input
            type="text"
            placeholder="   "
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>
      </div>

      <div className="user-card-grid">
        {paginatedUsers.length > 0 ? (
          paginatedUsers.map(user => (
            <div 
              key={user.user_id} 
              className="user-card"
              onClick={() => viewUserDetails(user.user_id)}
            >
              <div className="user-avatar">
                {user.name?.charAt(0).toUpperCase() || '?'}
              </div>
              <div className="user-info">
                <h3 className="user-name">{user.name}</h3>
                <p className="user-email">{user.email}</p>
                <p className="user-date">: {new Date(user.registered_at).toLocaleDateString()}</p>
              </div>
              <div className="user-card-action">
                <button className="view-details-btn"> </button>
              </div>
            </div>
          ))
        ) : (
          <div className="no-results">  .</div>
        )}
      </div>

      {/*   */}
      {totalPages > 1 && (
        <Pagination 
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={handlePageChange}
        />
      )}
    </div>
  );
}

export default UserList;