// src/components/modals/DeleteConfirmModal.js
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import Modal from '../common/Modal';
import '../../styles/common/DeleteConfirmModal.css';

function DeleteConfirmModal({ isOpen, onClose, onConfirm, title, message }) {
  const footer = (
    <>
      <button className="cancel-btn" onClick={onClose}></button>
      <button className="delete-btn" onClick={onConfirm}></button>
    </>
  );

  return (
    <Modal 
      isOpen={isOpen} 
      onClose={onClose} 
      title={title || ' '} 
      footer={footer}
      className="delete-confirm-modal"
    >
      <div className="warning-icon">
        <FontAwesomeIcon icon={faExclamationTriangle} />
      </div>
      <p className="confirm-message">{message || ' ?'}</p>
      <p className="warning-text">    .</p>
    </Modal>
  );
}

export default DeleteConfirmModal;