// src/pages/ResetPassword.js
import React, { useState, useEffect } from 'react';
import { useNavigate, useParams, useLocation } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLock, faExclamationTriangle, faCheckCircle } from '@fortawesome/free-solid-svg-icons';
import { authAPI } from '../services/api';
import '../styles/pages/ResetPassword.css';

function ResetPassword() {
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [resetSuccess, setResetSuccess] = useState(false);
  
  const navigate = useNavigate();
  const { token } = useParams(); // URL   

  useEffect(() => {
    //    
    if (!token) {
      setMessage('    .');
      setMessageType('error');
    }
  }, [token]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    //   
    if (!newPassword || !confirmPassword) {
      setMessage('  .');
      setMessageType('error');
      return;
    }
    
    if (newPassword !== confirmPassword) {
      setMessage('  .');
      setMessageType('error');
      return;
    }
    
    //    ()
    if (newPassword.length < 8) {
      setMessage('  8  .');
      setMessageType('error');
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      //  API   
      //    token  
      await authAPI.resetPassword(token, newPassword, confirmPassword);
      
      setMessage('  .   .');
      setMessageType('success');
      setResetSuccess(true);
      
      // 3    
      setTimeout(() => {
        navigate('/login');
      }, 3000);
    } catch (error) {
      console.error('  :', error);
      
      if (error.response) {
        if (error.response.status === 400) {
          setMessage('  .');
        } else if (error.response.status === 404) {
          setMessage('  .');
        } else if (error.response.status === 500) {
          setMessage('  .    .');
        } else {
          setMessage(error.response.data.message || '    .');
        }
      } else {
        setMessage('   .   .');
      }
      
      setMessageType('error');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="reset-password-container">
      <div className="reset-password-box">
        <h2> </h2>
        
        {message && (
          <div className={`message ${messageType === 'success' ? 'success-message' : 'error-message'}`}>
            <FontAwesomeIcon 
              icon={messageType === 'success' ? faCheckCircle : faExclamationTriangle} 
              className="message-icon" 
            />
            <span>{message}</span>
          </div>
        )}
        
        {!resetSuccess && token && (
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="new-password">
                <FontAwesomeIcon icon={faLock} className="input-icon" />
                 
              </label>
              <input
                type="password"
                id="new-password"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                required
                disabled={isSubmitting}
                placeholder="  "
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="confirm-password">
                <FontAwesomeIcon icon={faLock} className="input-icon" />
                 
              </label>
              <input
                type="password"
                id="confirm-password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                required
                disabled={isSubmitting}
                placeholder="  "
              />
            </div>
            
            <button 
              type="submit" 
              className="reset-btn"
              disabled={isSubmitting}
            >
              {isSubmitting ? ' ...' : ' '}
            </button>
          </form>
        )}
        
        <div className="return-login">
          <a href="/login" onClick={(e) => { e.preventDefault(); navigate('/login'); }}>
              
          </a>
        </div>
      </div>
    </div>
  );
}

export default ResetPassword;