// src/components/modals/LoginModal.js
import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faLock, faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import '../../styles/common/LoginModal.css';
import ForgotPasswordModal from './ForgotPasswordModal';

function LoginModal({ isOpen, onClose, onLogin }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [showForgotPassword, setShowForgotPassword] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    //   
    if (!email || !password) {
      setErrorMessage('   .');
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      //  
      await onLogin({ email, password });
      
      //     
      setEmail('');
      setPassword('');
      setErrorMessage('');
      onClose();
    } catch (error) {
      console.error(" :", error);
      
      //      
      if (error.response) {
        if (error.response.status === 401) {
          setErrorMessage('   .');
        } else if (error.response.status === 500) {
          setErrorMessage('  .    .');
        } else {
          //      
          setErrorMessage(error.response.data.message || '   .');
        }
      } else if (error.request) {
        //      
        setErrorMessage('   .   .');
      } else {
        //      
        setErrorMessage('    .');
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  const openForgotPassword = () => {
    setShowForgotPassword(true);
  };

  const closeForgotPassword = () => {
    setShowForgotPassword(false);
  };

  if (!isOpen) return null;

  return (
    <>
      <div className="modal-overlay">
        <div className="login-modal">
          <button className="close-button" onClick={onClose}>
            &times;
          </button>
          <div className="modal-header">
            <h2></h2>
            <p>  .</p>
          </div>
          {errorMessage && (
            <div className="error-container">
              <FontAwesomeIcon icon={faExclamationTriangle} className="error-icon" />
              <span>{errorMessage}</span>
            </div>
          )}
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="email">
                <FontAwesomeIcon icon={faEnvelope} className="input-icon" />
                
              </label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                disabled={isSubmitting}
                placeholder="  "
              />
            </div>
            <div className="form-group">
              <label htmlFor="password">
                <FontAwesomeIcon icon={faLock} className="input-icon" />
                
              </label>
              <input
                type="password"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                disabled={isSubmitting}
                placeholder=" "
              />
            </div>
            <div className="form-options">
              <div className="forgot-password" onClick={openForgotPassword}>
                 ?
              </div>
            </div>
            <button 
              type="submit" 
              className="login-submit-btn"
              disabled={isSubmitting}
            >
              {isSubmitting ? ' ...' : ''}
            </button>
          </form>
        </div>
      </div>

      {/*    */}
      <ForgotPasswordModal 
        isOpen={showForgotPassword} 
        onClose={closeForgotPassword} 
      />
    </>
  );
}

export default LoginModal;