/**
 *     API
 *   /api/admin/users/   JWT  .
 *   ,   ,       .
 */

import api from './api';

export const adminUserAPI = {
  /**
   *    
   * - Method: GET
   * - Endpoint: /api/admin/users
   * -         .
   */
  getAllUsers: async () => {
    try {
      const response = await api.get('/api/admin/users');
      return response.data;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  },

  /**
   *      
   * - Method: GET
   * - Endpoint: /api/admin/users/:user_id/saves/:slot_id
   * -        (Furniture, LP)  .
   *
   * @param {number} userId -   ID
   * @param {number} slotId -     (1, 2, 3)
   * @returns {Promise<Object>} -   
   */
  getUserSaveSlot: async (userId, slotId) => {
    try {
      const response = await api.get(`/api/admin/users/${userId}/saves/${slotId}`);
      return response.data;
    } catch (error) {
      console.error(` ID ${userId},  ${slotId}    :`, error);
      throw error;
    }
  },

  /**
   *     
   * - Method: GET
   * - Endpoint: /api/admin/users/:user_id/dialogs
   * -     .
   * - slot_id , npc_id   NPC  .
   * 
   * @param {number} userId -   ID
   * @param {number} slotId -     ()
   * @param {string} npcId -  NPC ID ()
   * @returns {Promise<Array>} -   
   */
  getUserDialogs: async (userId, slotId, npcId = null) => {
    try {
      if (!slotId) {
        throw new Error('slot_id  .');
      }
      
      let url = `/api/admin/users/${userId}/dialogs?slot_id=${slotId}`;
      if (npcId) {
        url += `&npc_id=${npcId}`;
      }
      
      const response = await api.get(url);
      return response.data;
    } catch (error) {
      console.error(` ID ${userId},  ${slotId}    :`, error);
      throw error;
    }
  },

  /**
   *     
   * - Method: DELETE
   * - Endpoint: /api/admin/users/:user_id/dialogs
   * -         .
   * 
   * @param {number} userId -   ID
   * @param {Array<Array>} logPairs -   ID   [[101, 102], [105, 106]]
   * @returns {Promise<Object>} -  
   */
  deleteDialogPairs: async (userId, logPairs) => {
    try {
      if (!logPairs || !Array.isArray(logPairs)) {
        throw new Error('log_pairs  .');
      }
      
      const response = await api.delete(`/api/admin/users/${userId}/dialogs`, {
        data: { log_pairs: logPairs }
      });
      return response.data;
    } catch (error) {
      console.error(` ID ${userId}    :`, error);
      throw error;
    }
  },

  /**
   *    
   * - Method: PATCH
   * - Endpoint: /api/admin/users/:user_id/saves/:slot_id
   * -     (,   ) .
   * - reputation_score money  .
   * 
   * @param {number} userId -   ID
   * @param {number} slotId -    
   * @param {Object} saveData -    (reputation_score, money )
   * @returns {Promise<Object>} -   
   */
  updateUserSave: async (userId, slotId, saveData) => {
    try {
      //   
      if (saveData.reputation_score === undefined || saveData.money === undefined) {
        throw new Error('reputation_score money  .');
      }

      const response = await api.patch(`/api/admin/users/${userId}/saves/${slotId}`, saveData);
      return response.data;
    } catch (error) {
      console.error(` ID ${userId},  ${slotId}    :`, error);
      throw error;
    }
  }
};

export default adminUserAPI;