// src/components/ai/TrainingForm.js -  
import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay, faInfoCircle, faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import { LoadingMessage, ErrorMessage } from '../common/StatusMessage';
import '../../styles/ai/TrainingForm.css';

function TrainingForm({ type, onSubmit, serverRunning }) {
  const [formData, setFormData] = useState(
    type === 'base' 
      ? {
          version: '',
          base_model: 'meta-llama/Llama-3.2-1B',
          source: 'beomi',
          train_path: '',
          val_path: '',
          epochs: 3,
          batch_size: 1,
          learning_rate: 0.00002,
          max_len: 1024,
          resume: false
        }
      : {
          version: '',
          npc: '',
          base_version: '',
          target: '',
          merge: false,
          resume: false,
          hyper: {
            epochs: 3,
            lr: 0.0002,
            bsz: 1,
            gradAcc: 8,
            maxLen: 1024
          }
        }
  );
  
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  //    
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    if (name.includes('.')) {
      const [parent, child] = name.split('.');
      setFormData({
        ...formData,
        [parent]: {
          ...formData[parent],
          [child]: type === 'number' ? Number(value) : value
        }
      });
    } else if (type === 'checkbox') {
      setFormData({
        ...formData,
        [name]: checked
      });
    } else if (type === 'number' || name === 'learning_rate') {
      setFormData({
        ...formData,
        [name]: Number(value)
      });
    } else {
      setFormData({
        ...formData,
        [name]: value
      });
    }
  };

  //    -     
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    //    
    if (type === 'base') {
      if (!formData.version || !formData.base_model) {
        setError('    .');
        return;
      }
      
      if (formData.source === 'custom' && (!formData.train_path || !formData.val_path)) {
        setError('         .');
        return;
      }
    } else if (type === 'lora') {
      if (!formData.version || !formData.npc || !formData.base_version || !formData.target) {
        setError(', NPC ID,   ,   ID  .');
        return;
      }
    }
    
    try {
      setLoading(true);
      setError(null);
      
      const result = await onSubmit(formData);
      if (result === null) {
        return;
      }
    } catch (err) {
      setError(`  : ${err.message || '   '}`);
      console.error('  :', err);
    } finally {
      setLoading(false);
    }
  };

  //      
  const renderServerRunningWarning = () => {
    if (!serverRunning) return null;
    
    return (
      <div className="server-running-warning">
        <FontAwesomeIcon icon={faExclamationTriangle} />
        <span>  .     .</span>
      </div>
    );
  };

  return (
    <form className="training-form" onSubmit={handleSubmit}>
      {loading && <LoadingMessage message="  ..." />}
      {error && <ErrorMessage message={error} />}
      {renderServerRunningWarning()}
      
      {type === 'base' && (
        <>
          <div className="form-group">
            <label htmlFor={`${type}-version`}>
               <span className="required">*</span>
            </label>
            <input
              type="text"
              id={`${type}-version`}
              name="version"
              value={formData.version}
              onChange={handleChange}
              placeholder="v1.5"
              required
            />
            <small className="form-hint">
                 (: v1.0, v1.5)
            </small>
          </div>
          
          <div className="form-group">
            <label htmlFor={`${type}-base-model`}>
                <span className="required">*</span>
            </label>
            <input
              type="text"
              id={`${type}-base-model`}
              name="base_model"
              value={formData.base_model}
              onChange={handleChange}
              placeholder="TinyLlama/TinyLlama-1.1B-Chat-v1.0"
              required
            />
            <small className="form-hint">
                 HuggingFace ID
            </small>
          </div>
          
          <div className="form-group">
            <label htmlFor="source"> </label>
            <select
              id="source"
              name="source"
              value={formData.source}
              onChange={handleChange}
            >
              <option value="beomi">  (beomi/KoAlpaca-RealQA)</option>
              <option value="jojo">    (jojo0217/korean_safe_conversation)</option>
              <option value="custom"> </option>
            </select>
            <small className="form-hint">
              <FontAwesomeIcon icon={faInfoCircle} /> 
              beomi jojo   .
            </small>
          </div>
          
          {formData.source === 'custom' && (
            <>
              <div className="form-group">
                <label htmlFor="train-path">
                     <span className="required">*</span>
                </label>
                <input
                  type="text"
                  id="train-path"
                  name="train_path"
                  value={formData.train_path}
                  onChange={handleChange}
                  placeholder="./data/train.json"
                  required
                />
                <small className="form-hint">
                      JSONL  
                </small>
              </div>
              
              <div className="form-group">
                <label htmlFor="val-path">
                     <span className="required">*</span>
                </label>
                <input
                  type="text"
                  id="val-path"
                  name="val_path"
                  value={formData.val_path}
                  onChange={handleChange}
                  placeholder="./data/val.json"
                  required
                />
                <small className="form-hint">
                      JSONL  
                </small>
              </div>
            </>
          )}
          
          <div className="form-group">
            <label htmlFor="resume" className="checkbox-label">
              <input
                type="checkbox"
                id="resume"
                name="resume"
                checked={formData.resume}
                onChange={handleChange}
              />
              <span>   </span>
            </label>
            <small className="form-hint">
              <FontAwesomeIcon icon={faInfoCircle} /> 
                    .
            </small>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="epochs"> </label>
              <input
                type="number"
                id="epochs"
                name="epochs"
                value={formData.epochs}
                onChange={handleChange}
                min="1"
                max="10"
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="batch-size"> </label>
              <input
                type="number"
                id="batch-size"
                name="batch_size"
                value={formData.batch_size}
                onChange={handleChange}
                min="1"
                max="8"
              />
            </div>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="learning-rate"></label>
              <input
                type="number"
                id="learning-rate"
                name="learning_rate"
                value={formData.learning_rate}
                onChange={handleChange}
                step="0.000001"
                min="0.000001"
                max="0.001"
              />
              <small className="form-hint">
                2e-5 0.00002 
              </small>
            </div>
            
            <div className="form-group">
              <label htmlFor="max-len"> </label>
              <input
                type="number"
                id="max-len"
                name="max_len"
                value={formData.max_len}
                onChange={handleChange}
                min="512"
                max="2048"
                step="128"
              />
            </div>
          </div>
        </>
      )}
      
      {type === 'lora' && (
        <>
          <div className="form-group">
            <label htmlFor="version">
               <span className="required">*</span>
            </label>
            <input
              type="text"
              id="version"
              name="version"
              value={formData.version}
              onChange={handleChange}
              placeholder="1"
              required
            />
            <small className="form-hint">
                 (: 1, 2, 3) -   "npc-base_version.version"  
            </small>
          </div>
          
          <div className="form-group">
            <label htmlFor="npc">
              NPC ID <span className="required">*</span>
            </label>
            <input
              type="text"
              id="npc"
              name="npc"
              value={formData.npc}
              onChange={handleChange}
              placeholder="silvia"
              required
            />
            <small className="form-hint">
               NPC ID (: silvia, sol)
            </small>
          </div>
          
          <div className="form-group">
            <label htmlFor="base_version">
                 <span className="required">*</span>
            </label>
            <input
              type="text"
              id="base_version"
              name="base_version"
              value={formData.base_version}
              onChange={handleChange}
              placeholder="v1.5"
              required
            />
            <small className="form-hint">
                  (: v1.5)
            </small>
          </div>
          
          <div className="form-group">
            <label htmlFor="target">
                ID <span className="required">*</span>
            </label>
            <input
              type="text"
              id="target"
              name="target"
              value={formData.target}
              onChange={handleChange}
              placeholder="silvia_chat"
              required
            />
            <small className="form-hint">
                   ID
            </small>
          </div>
          
          <div className="form-group">
            <label htmlFor="resume" className="checkbox-label">
              <input
                type="checkbox"
                id="resume"
                name="resume"
                checked={formData.resume}
                onChange={handleChange}
              />
              <span>   </span>
            </label>
            <small className="form-hint">
              <FontAwesomeIcon icon={faInfoCircle} /> 
                    .
            </small>
          </div>
          
          <div className="form-group">
            <label htmlFor="merge" className="checkbox-label">
              <input
                type="checkbox"
                id="merge"
                name="merge"
                checked={formData.merge}
                onChange={handleChange}
                disabled={true}
              />
              <span>     ( )</span>
            </label>
            <small className="form-hint">
              <FontAwesomeIcon icon={faInfoCircle} /> 
                LoRA   .
            </small>
          </div>
          
          <div className="hyper-params">
            <h4> </h4>
            
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="hyper-epochs"> </label>
                <input
                  type="number"
                  id="hyper-epochs"
                  name="hyper.epochs"
                  value={formData.hyper.epochs}
                  onChange={handleChange}
                  min="1"
                  max="10"
                />
              </div>
              
              <div className="form-group">
                <label htmlFor="hyper-lr"></label>
                <input
                  type="number"
                  id="hyper-lr"
                  name="hyper.lr"
                  value={formData.hyper.lr}
                  onChange={handleChange}
                  step="0.0001"
                  min="0.0001"
                  max="0.001"
                />
              </div>
            </div>
            
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="hyper-bsz"> </label>
                <input
                  type="number"
                  id="hyper-bsz"
                  name="hyper.bsz"
                  value={formData.hyper.bsz}
                  onChange={handleChange}
                  min="1"
                  max="8"
                />
              </div>
              
              <div className="form-group">
                <label htmlFor="hyper-gradAcc">Gradient Accumulation</label>
                <input
                  type="number"
                  id="hyper-gradAcc"
                  name="hyper.gradAcc"
                  value={formData.hyper.gradAcc}
                  onChange={handleChange}
                  min="1"
                  max="16"
                />
              </div>
            </div>
            
            <div className="form-group">
              <label htmlFor="hyper-maxLen">  </label>
              <input
                type="number"
                id="hyper-maxLen"
                name="hyper.maxLen"
                value={formData.hyper.maxLen}
                onChange={handleChange}
                min="512"
                max="2048"
                step="128"
              />
            </div>
          </div>
        </>
      )}
      
      <div className="form-actions">
        <button 
          type="submit" 
          className="submit-btn"
          disabled={loading}
        >
          <FontAwesomeIcon icon={faPlay} />
          {type === 'base' ? '   ' : 'LoRA  '}
        </button>
      </div>
    </form>
  );
}

export default TrainingForm;