// src/components/modals/DeleteModelModal.js
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import Modal from '../common/Modal';
import '../../styles/common/DeleteConfirmModal.css';

function DeleteModelModal({ isOpen, onClose, onConfirm, modelType, version }) {
  const footer = (
    <>
      <button className="cancel-btn" onClick={onClose}></button>
      <button className="delete-btn" onClick={onConfirm}></button>
    </>
  );

  const getModelTypeName = () => {
    switch(modelType) {
      case 'base':
        return ' ';
      case 'lora':
        return 'LoRA ';
      default:
        return '';
    }
  };

  return (
    <Modal 
      isOpen={isOpen} 
      onClose={onClose} 
      title="  " 
      footer={footer}
      className="delete-confirm-modal"
    >
      <div className="warning-icon">
        <FontAwesomeIcon icon={faExclamationTriangle} />
      </div>
      <p className="confirm-message">
        {getModelTypeName()} {version}() ?
      </p>
      <p className="warning-text">
            ,     .
      </p>
    </Modal>
  );
}

export default DeleteModelModal;