// src/pages/Settings.js
import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../contexts/AuthContext';
import '../styles/pages/Settings.css';

function Settings() {
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmNewPassword, setConfirmNewPassword] = useState('');
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  
  //      
  const [adminName, setAdminName] = useState('');
  const [adminEmail, setAdminEmail] = useState('');
  const [infoMessage, setInfoMessage] = useState('');
  const [infoMessageType, setInfoMessageType] = useState('');
  const [isInfoSubmitting, setIsInfoSubmitting] = useState(false);
  
  const { isLoggedIn, logout, changePassword, userData, updateAdminInfo } = useContext(AuthContext);
  const navigate = useNavigate();

  useEffect(() => {
    if (!isLoggedIn) {
      navigate('/');
    }
  }, [isLoggedIn, navigate]);

  //   
  useEffect(() => {
    if (userData) {
      setAdminName(userData.name || '');
      setAdminEmail(userData.email || '');
    }
  }, [userData]);

  const handleChangePassword = async () => {
    //  
    if (!currentPassword || !newPassword || !confirmNewPassword) {
      setMessage('  .');
      setMessageType('error');
      return;
    }

    if (newPassword !== confirmNewPassword) {
      setMessage('     .');
      setMessageType('error');
      return;
    }

    setIsSubmitting(true);

    try {
      //   
      await changePassword(currentPassword, newPassword);
      
      setMessage('  . 3  .');
      setMessageType('success');
      
      //   
      setCurrentPassword('');
      setNewPassword('');
      setConfirmNewPassword('');
      
      // 3     
      setTimeout(() => {
        logout();
        navigate('/');
      }, 3000);
    } catch (error) {
      console.error('  :', error);
      
      if (error.response) {
        if (error.response.status === 400) {
          setMessage('  .');
        } else if (error.response.status === 403) {
          setMessage('   .');
        } else if (error.response.status === 404) {
          setMessage('   .');
        } else if (error.response.status === 500) {
          setMessage('  .    .');
        } else {
          setMessage(error.response.data.message || '    .');
        }
      } else {
        setMessage('   .   .');
      }
      
      setMessageType('error');
    } finally {
      setIsSubmitting(false);
    }
  };

  //    
  const handleUpdateAdminInfo = async () => {
    const updateData = {};
    
    //   
    if (adminName !== userData.name) {
      updateData.admin_name = adminName;
    }
    
    if (adminEmail !== userData.email) {
      updateData.email = adminEmail;
    }
    
    //   
    if (Object.keys(updateData).length === 0) {
      setInfoMessage('  .');
      setInfoMessageType('error');
      return;
    }
    
    setIsInfoSubmitting(true);
    
    try {
      await updateAdminInfo(updateData);
      
      setInfoMessage('   .');
      setInfoMessageType('success');
    } catch (error) {
      console.error('   :', error);
      
      if (error.response) {
        if (error.response.status === 400) {
          setInfoMessage('  .');
        } else if (error.response.status === 409) {
          setInfoMessage('   .');
        } else if (error.response.status === 500) {
          setInfoMessage('  .    .');
        } else {
          setInfoMessage(error.response.data.message || '    .');
        }
      } else {
        setInfoMessage('   .   .');
      }
      
      setInfoMessageType('error');
    } finally {
      setIsInfoSubmitting(false);
    }
  };

  return (
    <div className="settings-container">
      <header className="settings-header">
        <h1> </h1>
      </header>

      {/*      */}
      <section className="admin-info-section">
        <h2>  </h2>
        <div className="form-group">
          <label htmlFor="adminName"> </label>
          <input
            type="text"
            id="adminName"
            value={adminName}
            onChange={(e) => setAdminName(e.target.value)}
            disabled={isInfoSubmitting}
          />
        </div>
        <div className="form-group">
          <label htmlFor="adminEmail"></label>
          <input
            type="email"
            id="adminEmail"
            value={adminEmail}
            onChange={(e) => setAdminEmail(e.target.value)}
            disabled={isInfoSubmitting}
          />
        </div>
        <button 
          className="update-info-btn" 
          onClick={handleUpdateAdminInfo}
          disabled={isInfoSubmitting}
        >
          {isInfoSubmitting ? ' ...' : ' '}
        </button>
        {infoMessage && (
          <p className={`message ${infoMessageType === 'success' ? 'success-message' : 'error-message'}`}>
            {infoMessage}
          </p>
        )}
      </section>

      <section className="password-change-section">
        <h2> </h2>
        <div className="form-group">
          <label htmlFor="currentPassword"> </label>
          <input
            type="password"
            id="currentPassword"
            value={currentPassword}
            onChange={(e) => setCurrentPassword(e.target.value)}
            disabled={isSubmitting}
          />
        </div>
        <div className="form-group">
          <label htmlFor="newPassword"> </label>
          <input
            type="password"
            id="newPassword"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            disabled={isSubmitting}
          />
        </div>
        <div className="form-group">
          <label htmlFor="confirmNewPassword">  </label>
          <input
            type="password"
            id="confirmNewPassword"
            value={confirmNewPassword}
            onChange={(e) => setConfirmNewPassword(e.target.value)}
            disabled={isSubmitting}
          />
        </div>
        <button 
          className="change-password-btn" 
          onClick={handleChangePassword}
          disabled={isSubmitting}
        >
          {isSubmitting ? ' ...' : ' '}
        </button>
        {message && (
          <p className={`message ${messageType === 'success' ? 'success-message' : 'error-message'}`}>
            {message}
          </p>
        )}
      </section>
    </div>
  );
}

export default Settings;