// src/contexts/AuthContext.js
import React, { createContext, useState, useEffect, useCallback } from 'react';
import { authAPI } from '../services/api';

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userData, setUserData] = useState(null);
  const [loading, setLoading] = useState(true);

  //        
  useEffect(() => {
    const token = localStorage.getItem('token');
    
    const loadUserData = async () => {
      if (token) {
        try {
          const data = await authAPI.getAdminInfo();
          // admin_name  
          setUserData({
            id: data.admin.admin_id,
            email: data.admin.email,
            name: data.admin.admin_name || '' // admin_name  
          });
          setIsLoggedIn(true);
        } catch (error) {
          console.error('  :', error);
          localStorage.removeItem('token');
        }
      }
      setLoading(false);
    };

    loadUserData();
  }, []);

  //  
  const login = useCallback(async (user) => {
    try {
      const response = await authAPI.login(user.email, user.password);
      
      //  
      localStorage.setItem('token', response.token);
      
      //   
      const userInfo = await authAPI.getAdminInfo();
      setUserData({
        id: userInfo.admin.admin_id,
        email: userInfo.admin.email,
        name: userInfo.admin.admin_name || ''
      });
      setIsLoggedIn(true);
      return true;
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  }, []);

  //  
  const logout = useCallback(async () => {
    try {
      await authAPI.logout();
    } catch (error) {
      console.error(' API  :', error);
      // API       
    } finally {
      localStorage.removeItem('token');
      setIsLoggedIn(false);
      setUserData(null);
    }
  }, []);

  //     ( )
  const updateAdminInfo = useCallback(async (updateData) => {
    try {
      await authAPI.updateAdminInfo(updateData);
      
      //      
      const updatedInfo = await authAPI.getAdminInfo();
      setUserData({
        id: updatedInfo.admin.admin_id,
        email: updatedInfo.admin.email,
        name: updatedInfo.admin.admin_name || ''
      });
      
      return true;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }, []);

  //   
  const changePassword = useCallback(async (currentPassword, newPassword) => {
    try {
      await authAPI.changePassword(
        currentPassword, 
        newPassword, 
        newPassword //    
      );
      return true;
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }, []);

  //   
  const forgotPassword = useCallback(async (email) => {
    try {
      await authAPI.forgotPassword(email);
      return true;
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  }, []);

  const value = {
    isLoggedIn,
    userData,
    loading,
    login,
    logout,
    updateAdminInfo, //  
    changePassword,
    forgotPassword
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};