// src/pages/Home.js
import React, { useState, useEffect, useContext } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faSignOutAlt,
  faClock,
  faUser,
  faCalendarAlt,
  faGlassMartini,
  faUserCheck
} from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import '../styles/pages/Home.css';
import { AuthContext } from '../contexts/AuthContext';
import { DataContext } from '../contexts/DataContext';

function Home() {
  const [currentTime, setCurrentTime] = useState('');
  const [currentDate, setCurrentDate] = useState('');
  const { isLoggedIn, userData, logout } = useContext(AuthContext);
  const { 
    users, 
    cocktails, 
    getWeeklyNewUsers,  // : getTodayNewUsers -> getWeeklyNewUsers
    getWeeklyNewCocktails,
    loading,
    refreshData
  } = useContext(DataContext);
  
  const navigate = useNavigate();

  //  
  useEffect(() => {
    const updateDateTime = () => {
      const now = new Date();

      const hours = String(now.getHours()).padStart(2, '0');
      const minutes = String(now.getMinutes()).padStart(2, '0');
      const seconds = String(now.getSeconds()).padStart(2, '0');
      setCurrentTime(`${hours}:${minutes}:${seconds}`);

      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, '0');
      const day = String(now.getDate()).padStart(2, '0');
      const dayNames = ['', '', '', '', '', '', ''];
      const dayName = dayNames[now.getDay()];
      setCurrentDate(`${year} ${month} ${day} (${dayName})`);
    };

    updateDateTime();
    const timer = setInterval(updateDateTime, 1000);

    return () => clearInterval(timer);
  }, []);

  //    
  useEffect(() => {
    // 30  
    const refreshInterval = setInterval(() => {
      refreshData();
    }, 30000);
    
    return () => clearInterval(refreshInterval);
  }, [refreshData]);

  const goToUsers = () => {
    navigate('/users');
  };

  const goToCocktails = () => {
    navigate('/cocktails');
  };

  const handleLogout = async () => {
    try {
      await logout();
      navigate('/login');
    } catch (error) {
      console.error(" :", error);
    }
  };

  return (
    <>
      <header className="content-header">
        <h1></h1>
        {isLoggedIn && (
          <div className="user-profile">
            <span className="user-name">
              <FontAwesomeIcon icon={faUser} /> {userData?.name || ''}
            </span>
            <button className="login-btn" onClick={handleLogout}>
              <FontAwesomeIcon icon={faSignOutAlt} /> 
            </button>
          </div>
        )}
      </header>

      <div className="dashboard-container">
        {/*    */}
        <section className="summary-grid">
          <div className="summary-card summary-total-users">
            <div className="summary-icon">
              <FontAwesomeIcon icon={faUser} />
            </div>
            <div className="summary-content">
              <h3> </h3>
              <p id="total-users">{loading ? ' ...' : users.length}</p>
            </div>
          </div>
          <div className="summary-card summary-total-cocktails">
            <div className="summary-icon">
              <FontAwesomeIcon icon={faGlassMartini} />
            </div>
            <div className="summary-content">
              <h3> </h3>
              <p id="total-cocktails">{loading ? ' ...' : cocktails.length}</p>
            </div>
          </div>
          <div className="summary-card summary-new-users">
            <div className="summary-icon">
              <FontAwesomeIcon icon={faUserCheck} />
            </div>
            <div className="summary-content">
              <h3>  ( )</h3>
              <p id="new-users">{loading ? ' ...' : getWeeklyNewUsers()}</p> {/* : getTodayNewUsers -> getWeeklyNewUsers */}
            </div>
          </div>
          <div className="summary-card summary-new-cocktails">
            <div className="summary-icon">
              <FontAwesomeIcon icon={faGlassMartini} />
            </div>
            <div className="summary-content">
              <h3>  ( )</h3>
              <p id="new-cocktails">{loading ? ' ...' : getWeeklyNewCocktails()}</p>
            </div>
          </div>
        </section>

        {/*   -   */}
        <section className="lists">
          {/*   */}
          <div className="list-container">
            <h2> </h2>
            <UserTable users={users.slice(0, 5)} loading={loading} />
            <button className="more-btn" onClick={goToUsers}> </button>
          </div>

          {/*   */}
          <div className="list-container">
            <h2> </h2>
            <CocktailTable cocktails={cocktails.slice(0, 5)} loading={loading} />
            <button className="more-btn" onClick={goToCocktails}> </button>
          </div>
        </section>

        {/*    */}
        <div className="time-display">
          <div className="time-container">
            <FontAwesomeIcon icon={faClock} />
            <span id="current-time">{currentTime}</span>
          </div>
          <div className="date-container">
            <FontAwesomeIcon icon={faCalendarAlt} />
            <span id="current-date">{currentDate}</span>
          </div>
        </div>
      </div>
    </>
  );
}

//    -   
function UserTable({ users, loading }) {
  if (loading) {
    return <div className="loading-table">  ...</div>;
  }
  
  if (!users || users.length === 0) {
    return <div className="empty-table">   .</div>;
  }
  
  return (
    <div className="table-responsive">
      <table id="user-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {users.map(user => (
            <tr key={user.user_id}>
              <td>{user.name}</td>
              <td>{user.email}</td>
              <td>{new Date(user.registered_at).toLocaleDateString()}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

//    -   
function CocktailTable({ cocktails, loading }) {
  if (loading) {
    return <div className="loading-table">  ...</div>;
  }
  
  if (!cocktails || cocktails.length === 0) {
    return <div className="empty-table">   .</div>;
  }
  
  return (
    <div className="table-responsive">
      <table id="cocktail-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {cocktails.map(cocktail => (
            <tr key={cocktail.recipe_id}>
              <td>{cocktail.name}</td>
              <td>{cocktail.method}</td>
              <td>{cocktail.type === 'basic' ? '' : ''}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Home;