import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faPlay, 
  faStop, 
  faRedoAlt,
  faCircle, 
  faInfoCircle 
} from '@fortawesome/free-solid-svg-icons';
import { aiAPI } from '../../services/aiAPI';
import { LoadingMessage, ErrorMessage, SuccessMessage } from '../common/StatusMessage';
import '../../styles/ai/ServerControl.css';

function ServerControl({ serverStatus, onRefreshStatus, isRefreshing }) {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);

  //  
  const handleStartServer = async () => {
    try {
      setLoading(true);
      setError(null);
      setSuccess(null);
      
      await aiAPI.startFlaskServer();
      setSuccess('Flask   .');
      
      //        
      setTimeout(() => {
        onRefreshStatus();
      }, 1000);
      
    } catch (err) {
      setError('Flask   ');
      console.error('  :', err);
    } finally {
      setLoading(false);
    }
  };

  //  
  const handleStopServer = async () => {
    try {
      setLoading(true);
      setError(null);
      setSuccess(null);
      
      await aiAPI.stopFlaskServer();
      setSuccess('Flask   .');
      
      //      
      setTimeout(() => {
        onRefreshStatus();
      }, 500);
      
    } catch (err) {
      setError('Flask   ');
      console.error('  :', err);
    } finally {
      setLoading(false);
    }
  };

  //  
  const handleRefresh = async () => {
    console.log('  ');
    await onRefreshStatus();
  };

  //   
  const renderServerStatus = () => {
    if (!serverStatus) {
      return <div className="server-status unknown">  </div>;
    }

    const isRunning = serverStatus.status === 'running';
    const isStopped = serverStatus.status === 'stopped';
    
    return (
      <div className={`server-status ${isRunning ? 'running' : isStopped ? 'stopped' : 'unknown'}`}>
        <FontAwesomeIcon icon={faCircle} className="status-icon" />
        <span className="status-text">
          {isRunning && ' '}
          {isStopped && ''}
          {!isRunning && !isStopped && '  '}
        </span>
      </div>
    );
  };

  //     
  const renderActiveModelInfo = () => {
    if (!serverStatus || serverStatus.status !== 'running') {
      return null;
    }

    return (
      <div className="active-model-info">
        <FontAwesomeIcon icon={faInfoCircle} className="info-icon" />
        <div className="model-info-content">
          <p><strong>   :</strong> {serverStatus.active_base || ' '}</p>
          {serverStatus.active_npcs && serverStatus.active_npcs.length > 0 && (
            <p><strong> NPC:</strong> {serverStatus.active_npcs.join(', ')}</p>
          )}
          {serverStatus.model && (
            <p><strong> :</strong> {serverStatus.model}</p>
          )}
          {serverStatus.cuda && (
            <p><strong>GPU:</strong> {serverStatus.device} (CUDA )</p>
          )}
          {serverStatus.port && (
            <p><strong>:</strong> {serverStatus.port}</p>
          )}
        </div>
      </div>
    );
  };

  return (
    <div className="server-control-container">
      {loading && <LoadingMessage message="   ..." />}
      {error && <ErrorMessage message={error} />}
      {success && <SuccessMessage message={success} />}
      
      <div className="server-control-layout">
        <div className="server-status-section">
          <div className="status-header">
            <h3>Flask  </h3>
            <button 
              className="refresh-btn" 
              onClick={handleRefresh} 
              disabled={loading || isRefreshing}
            >
              <FontAwesomeIcon icon={faRedoAlt} spin={isRefreshing} />
              {isRefreshing ? ' ...' : ''}
            </button>
          </div>
          
          {renderServerStatus()}
          
          <div className="server-actions">
            <button 
              className="start-btn" 
              onClick={handleStartServer} 
              disabled={loading || (serverStatus && serverStatus.status === 'running')}
            >
              <FontAwesomeIcon icon={faPlay} />
               
            </button>
            
            <button 
              className="stop-btn" 
              onClick={handleStopServer} 
              disabled={loading || (serverStatus && serverStatus.status !== 'running')}
            >
              <FontAwesomeIcon icon={faStop} />
               
            </button>
          </div>
        </div>
        
        {renderActiveModelInfo()}
      </div>
    </div>
  );
}

export default ServerControl;