// src/components/layout/Weather.js
import React, { useState, useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
  faSun, 
  faCloud, 
  faCloudRain, 
  faSnowflake, 
  faCloudBolt, 
  faCloudSun,
  faWind,
  faQuestion,
  faSpinner
} from '@fortawesome/free-solid-svg-icons';
import '../../styles/layout/Weather.css';

function Weather() {
  const [weather, setWeather] = useState({
    temperature: null,
    description: '',
    city: '  ...',
    condition: '',
    weatherCode: '',
    loading: true,
    error: false
  });

  //   
  const getCachedLocation = () => {
    const cachedLocation = localStorage.getItem('weatherLocation');
    const cacheTimestamp = localStorage.getItem('weatherLocationTimestamp');
    const now = Date.now();
    
    // 6  (21600000 )
    if (cachedLocation && cacheTimestamp && (now - parseInt(cacheTimestamp) < 21600000)) {
      return JSON.parse(cachedLocation);
    }
    
    return null;
  };
  
  //   
  const setCachedLocation = (latitude, longitude) => {
    const locationData = { latitude, longitude };
    localStorage.setItem('weatherLocation', JSON.stringify(locationData));
    localStorage.setItem('weatherLocationTimestamp', Date.now().toString());
  };
  
  // API    
  const throttle = (func, limit) => {
    let lastCall = 0;
    return function(...args) {
      const now = Date.now();
      if (now - lastCall >= limit) {
        lastCall = now;
        return func.apply(this, args);
      }
    }
  };

  //     
  const translateWeatherDescription = (englishDesc) => {
    const translations = {
      // 
      'sunny': '',
      'clear': '',
      'fine': '',
      
      // /
      'partly cloudy': ' ',
      'cloudy': ' ',
      'overcast': '',
      'fog': '',
      'mist': '',
      'haze': '',
      
      // 
      'light rain': ' ',
      'moderate rain': '',
      'heavy rain': ' ',
      'rain': '',
      'light drizzle': '',
      'drizzle': '',
      'patchy rain': ' ',
      'showers': '',
      
      // 
      'snow': '',
      'light snow': ' ',
      'heavy snow': '',
      'sleet': '',
      
      // 
      'thunderstorm': '',
      'thundery outbreaks': '',
      'thunder': ''
    };
    
    //    
    const lowerDesc = englishDesc.toLowerCase();
    
    //    
    for (const [eng, kor] of Object.entries(translations)) {
      if (lowerDesc === eng) {
        return kor;
      }
    }
    
    //   
    for (const [eng, kor] of Object.entries(translations)) {
      if (lowerDesc.includes(eng)) {
        return kor;
      }
    }
    
    //    
    return englishDesc;
  };

  useEffect(() => {
    //    
    const fetchWeather = async () => {
      try {
        setWeather(prev => ({ ...prev, loading: true }));
        
        //    
        const cachedLocation = getCachedLocation();
        
        if (cachedLocation) {
          //    
          await fetchWeatherByCoords(cachedLocation.latitude, cachedLocation.longitude);
        } else {
          //    
          navigator.geolocation.getCurrentPosition(
            async (position) => {
              const { latitude, longitude } = position.coords;
              
              //   
              setCachedLocation(latitude, longitude);
              
              //    
              await fetchWeatherByCoords(latitude, longitude);
            },
            (err) => {
              console.error('   :', err);
              //      () 
              fetchWeatherByCity('');
            },
            {
              enableHighAccuracy: false, //   
              maximumAge: 21600000,      // 6  
              timeout: 10000             // 10 
            }
          );
        }
      } catch (error) {
        console.error('     :', error);
        setWeather(prev => ({
          ...prev,
          loading: false,
          error: true
        }));
      }
    };

    // ,    
    const fetchWeatherByCoords = throttle(async (latitude, longitude) => {
      try {
        //      
        const cityResponse = await fetch(
          `https://nominatim.openstreetmap.org/reverse?format=json&lat=${latitude}&lon=${longitude}&zoom=10&addressdetails=1&accept-language=ko`
        );
        
        if (!cityResponse.ok) {
          throw new Error('    .');
        }
        
        const locationData = await cityResponse.json();
        
        //   / 
        const city = locationData.address.city || 
                     locationData.address.town || 
                     locationData.address.village || 
                     '  ';
        const district = locationData.address.suburb || 
                         locationData.address.county || 
                         locationData.address.state_district || 
                         '';
        
        //    (wttr.in API  - API   )
        const weatherResponse = await fetch(
          `https://wttr.in/${latitude},${longitude}?format=j1`
        );
        
        if (!weatherResponse.ok) {
          throw new Error('    .');
        }
        
        const weatherData = await weatherResponse.json();
        const current = weatherData.current_condition[0];
        
        //     
        const koreanDescription = translateWeatherDescription(current.weatherDesc[0].value);
        
        //   
        setWeather({
          temperature: current.temp_C,
          description: koreanDescription,
          city: district ? `${city} ${district}` : city,
          condition: koreanDescription,
          weatherCode: current.weatherCode,
          loading: false,
          error: false
        });
      } catch (error) {
        console.error('       :', error);
        setWeather(prev => ({
          ...prev,
          loading: false,
          error: true
        }));
      }
    }, 1000);

    //     
    const fetchWeatherByCity = throttle(async (city) => {
      try {
        // wttr.in API  (API   )
        const response = await fetch(
          `https://wttr.in/${city}?format=j1`
        );
        
        if (!response.ok) {
          throw new Error('    .');
        }
        
        const data = await response.json();
        const current = data.current_condition[0];
        
        //     
        const koreanDescription = translateWeatherDescription(current.weatherDesc[0].value);
        
        setWeather({
          temperature: current.temp_C,
          description: koreanDescription,
          city: city,
          condition: koreanDescription,
          weatherCode: current.weatherCode,
          loading: false,
          error: false
        });
      } catch (error) {
        console.error('      :', error);
        setWeather(prev => ({
          ...prev,
          loading: false,
          error: true
        }));
      }
    }, 1000);

    fetchWeather();
    
    // 30   
    const interval = setInterval(fetchWeather, 30 * 60 * 1000);
    
    return () => clearInterval(interval);
  }, []);

  //      
  const getWeatherIconWithColor = () => {
    const { condition, weatherCode } = weather;
    
    //        
    let icon = null;
    let color = "#ffeb3b"; //   ()
    
    //      
    const conditionLower = condition.toLowerCase();
    
    // /  
    if (
      conditionLower.includes('') || 
      conditionLower.includes('') || 
      conditionLower.includes('overcast') || 
      conditionLower.includes('cloudy')
    ) {
      //  
      if (
        conditionLower.includes('') || 
        conditionLower.includes('overcast') || 
        conditionLower.includes(' ')
      ) {
        icon = faCloud;
        color = "#78909c"; // 
      }
      //  
      else {
        icon = faCloudSun;
        color = "#90a4ae"; //  
      }
    }
    
    //   
    else if (
      conditionLower.includes('') || 
      conditionLower.includes('') || 
      conditionLower.includes('rain') || 
      conditionLower.includes('shower') || 
      conditionLower.includes('drizzle')
    ) {
      icon = faCloudRain;
      color = "#4fc3f7"; // 
    }
    
    //   
    else if (
      conditionLower.includes('') || 
      conditionLower.includes('') || 
      conditionLower.includes('snow') || 
      conditionLower.includes('blizzard')
    ) {
      icon = faSnowflake;
      color = "#e1f5fe"; //  
    }
    
    //   
    else if (
      conditionLower.includes('') || 
      conditionLower.includes('') || 
      conditionLower.includes('') || 
      conditionLower.includes('thunder') || 
      conditionLower.includes('lightning')
    ) {
      icon = faCloudBolt;
      color = "#ffca28"; // 
    }
    
    //   
    else if (
      conditionLower.includes('') || 
      conditionLower.includes('') || 
      conditionLower.includes('fog') || 
      conditionLower.includes('mist') || 
      conditionLower.includes('haze')
    ) {
      icon = faWind;
      color = "#b0bec5"; //  
    }
    
    //  
    else if (
      conditionLower.includes('') || 
      conditionLower.includes('') || 
      conditionLower.includes('sunny') || 
      conditionLower.includes('clear')
    ) {
      icon = faSun;
      color = "#ffeb3b"; // 
    }
    
    //       
    else {
      const code = parseInt(weatherCode);
      
      // wttr.in   
      if ([113].includes(code)) { // 
        icon = faSun;
        color = "#ffeb3b"; // 
      }
      else if ([116].includes(code)) { //  
        icon = faCloudSun;
        color = "#90a4ae"; //  
      }
      else if ([119].includes(code)) { //  
        icon = faCloud;
        color = "#78909c"; // 
      }
      else if ([122, 143].includes(code)) { // 
        icon = faCloud;
        color = "#78909c"; // 
      }
      else if ([176, 263, 266, 281, 284, 293, 296, 299, 302, 305, 308, 311, 314, 353, 356, 359].includes(code)) { // 
        icon = faCloudRain;
        color = "#4fc3f7"; // 
      }
      else if ([179, 227, 230, 323, 326, 329, 332, 335, 338, 368, 371].includes(code)) { // 
        icon = faSnowflake;
        color = "#e1f5fe"; //  
      }
      else if ([200, 386, 389, 392, 395].includes(code)) { // 
        icon = faCloudBolt;
        color = "#ffca28"; // 
      }
      else if ([143, 248, 260].includes(code)) { // 
        icon = faWind;
        color = "#b0bec5"; //  
      }
      else { //     
        icon = faQuestion;
        color = "#9e9e9e"; //  
      }
    }
    
    return { icon, color };
  };

  if (weather.loading) {
    return (
      <div className="weather-widget">
        <FontAwesomeIcon icon={faSpinner} spin className="weather-icon" />
        <div className="weather-info">
          <div className="weather-location">   ...</div>
        </div>
      </div>
    );
  }

  if (weather.error) {
    return (
      <div className="weather-widget">
        <div className="weather-icon">
          <FontAwesomeIcon icon={faQuestion} />
        </div>
        <div className="weather-info">
          <div className="weather-location">  </div>
          <div className="weather-desc">   .</div>
        </div>
      </div>
    );
  }

  const { icon, color } = getWeatherIconWithColor();

  return (
    <div className="weather-widget">
      <div className="weather-icon" style={{ color }}>
        <FontAwesomeIcon icon={icon} />
      </div>
      <div className="weather-info">
        <div className="weather-location">{weather.city}</div>
        <div className="weather-temp">{Math.round(weather.temperature)}C</div>
        <div className="weather-desc">{weather.description}</div>
      </div>
    </div>
  );
}

export default Weather;