  // src/pages/Login.js
  import React, { useState, useContext, useEffect } from 'react';
  import { useNavigate } from 'react-router-dom';
  import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
  import { 
    faEnvelope, 
    faLock, 
    faExclamationTriangle,
    faGlassMartini
  } from '@fortawesome/free-solid-svg-icons';
  import { AuthContext } from '../contexts/AuthContext';
  import ForgotPasswordModal from '../components/modals/ForgotPasswordModal';
  import '../styles/pages/Login.css';

  function Login() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const [showForgotPassword, setShowForgotPassword] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [rememberMe, setRememberMe] = useState(false);
    const { isLoggedIn, login } = useContext(AuthContext);
    const navigate = useNavigate();
    
    //      
    useEffect(() => {
      if (isLoggedIn) {
        navigate('/');
      }
      
      //   
      const savedEmail = localStorage.getItem('rememberedEmail');
      if (savedEmail) {
        setEmail(savedEmail);
        setRememberMe(true);
      }
    }, [isLoggedIn, navigate]);

    const handleSubmit = async (e) => {
      e.preventDefault();
      
      //   
      if (!email || !password) {
        setErrorMessage('   .');
        return;
      }
      
      //   
      if (rememberMe) {
        localStorage.setItem('rememberedEmail', email);
      } else {
        localStorage.removeItem('rememberedEmail');
      }
      
      setIsSubmitting(true);
      
      try {
        //  
        await login({ email, password });
        //      (useEffect )
      } catch (error) {
        console.error(" :", error);
        
        //      
        if (error.response) {
          if (error.response.status === 401) {
            setErrorMessage('   .');
          } else if (error.response.status === 500) {
            setErrorMessage('  .    .');
          } else {
            setErrorMessage(error.response.data.message || '   .');
          }
        } else if (error.request) {
          setErrorMessage('   .   .');
        } else {
          setErrorMessage('    .');
        }
      } finally {
        setIsSubmitting(false);
      }
    };

    return (
      <div className="login-page">
        <div className="wave wave1"></div>
        <div className="wave wave2"></div>
        <div className="login-container">
          {/*    */}
          <div className="brand-logo">
            <FontAwesomeIcon icon={faGlassMartini} className="logo-icon" />
            <h1>FLAPPER MOONSHINE</h1>
            <p> </p>
          </div>
          
          <div className="login-header">
            <h1> </h1>
            <p> </p>
          </div>
          
          {errorMessage && (
            <div className="error-container">
              <FontAwesomeIcon icon={faExclamationTriangle} className="error-icon" />
              <span>{errorMessage}</span>
            </div>
          )}
          
          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-group">
              <label htmlFor="email">
                <FontAwesomeIcon icon={faEnvelope} className="input-icon" />
                
              </label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                disabled={isSubmitting}
                placeholder="  "
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="password">
                <FontAwesomeIcon icon={faLock} className="input-icon" />
                
              </label>
              <input
                type="password"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                disabled={isSubmitting}
                placeholder=" "
              />
            </div>
            
            <div className="form-options">
              <div className="remember-me">
                <input
                  type="checkbox"
                  id="remember-me"
                  checked={rememberMe}
                  onChange={(e) => setRememberMe(e.target.checked)}
                />
                <label htmlFor="remember-me"> </label>
              </div>
              
              <div className="forgot-password" onClick={() => setShowForgotPassword(true)}>
                 ?
              </div>
            </div>
            
            <button 
              type="submit" 
              className="login-submit-btn"
              disabled={isSubmitting}
            >
              {isSubmitting ? ' ...' : ''}
            </button>
          </form>
          
          {/*   */}
          <div className="login-footer">
            <div className="version"> 1.0.0</div>
            <div className="copyright"> 2025 FLAPPER MOONSHINE. All rights reserved.</div>
          </div>
        </div>
        
        {/*    */}
        <ForgotPasswordModal 
          isOpen={showForgotPassword} 
          onClose={() => setShowForgotPassword(false)} 
        />
      </div>
    );
  }

  export default Login;