// src/components/modals/ForgotPasswordModal.js
import React, { useState, useContext } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faExclamationTriangle, faCheckCircle } from '@fortawesome/free-solid-svg-icons';
import { AuthContext } from '../../contexts/AuthContext';
import '../../styles/common/LoginModal.css';

function ForgotPasswordModal({ isOpen, onClose }) {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const { forgotPassword } = useContext(AuthContext);

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!email) {
      setMessage(' .');
      setMessageType('error');
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      await forgotPassword(email);
      
      setMessage('    .');
      setMessageType('success');
      setEmail(''); //   
    } catch (error) {
      console.error('   :', error);
      
      if (error.response) {
        if (error.response.status === 404) {
          setMessage('  .');
        } else if (error.response.status === 429) {
          setMessage('  .    .');
        } else if (error.response.status === 500) {
          setMessage('   .   .');
        } else {
          setMessage(error.response.data.message || '    .');
        }
      } else {
        setMessage('   .   .');
      }
      
      setMessageType('error');
    } finally {
      setIsSubmitting(false);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay">
      <div className="login-modal">
        <button className="close-button" onClick={onClose}>
          &times;
        </button>
        <div className="modal-header">
          <h2> </h2>
          <p>      .</p>
        </div>
        
        {message && (
          <div className={`message-container ${messageType === 'success' ? 'success-message' : 'error-container'}`}>
            <FontAwesomeIcon 
              icon={messageType === 'success' ? faCheckCircle : faExclamationTriangle} 
              className="message-icon" 
            />
            <span>{message}</span>
          </div>
        )}
        
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="email">
              <FontAwesomeIcon icon={faEnvelope} className="input-icon" />
              
            </label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              disabled={isSubmitting}
              placeholder="  "
            />
          </div>
          
          <button 
            type="submit" 
            className="login-submit-btn"
            disabled={isSubmitting}
          >
            {isSubmitting ? ' ...' : ' '}
          </button>
        </form>
      </div>
    </div>
  );
}

export default ForgotPasswordModal;