// src/hooks/useTrainingStatus.js
import { useState, useEffect, useRef } from 'react';
import { aiAPI } from '../services/aiAPI';

export const useTrainingStatus = (version) => {
  const [status, setStatus] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const pollingRef = useRef(null);
  const isFetchingRef = useRef(false); //    
  const lastFetchTimeRef = useRef(0); //    
  const MIN_FETCH_INTERVAL = 2000; //    (2)
  const INITIAL_POLLING_INTERVAL = 10000; //    (10)
  const [pollingInterval, setPollingInterval] = useState(INITIAL_POLLING_INTERVAL);

  //    
  const startPolling = () => {
    //   
    stopPolling();

    //  
    const fetchStatus = async () => {
      //              
      const now = Date.now();
      if (isFetchingRef.current || (now - lastFetchTimeRef.current < MIN_FETCH_INTERVAL)) {
        console.log('       ,  ');
        return;
      }
      
      isFetchingRef.current = true; //   
      lastFetchTimeRef.current = now; //    
      
      try {
        console.log(`[${version}]    ...`);
        const data = await aiAPI.getTrainingStatus(version);
        setStatus(data);
        setLoading(false);

        //       
        adjustPollingInterval(data);

        //       
        if (data.status === 'completed' || data.status === 'failed') {
          console.log(`[${version}]    ,  `);
          stopPolling();
        }
      } catch (err) {
        console.error(`[${version}]    :`, err);
        setError(err.message || '   ');
        setLoading(false);
        
        //      
        setPollingInterval(prev => Math.min(prev * 1.5, 30000)); //  30
      } finally {
        isFetchingRef.current = false; //   
      }
    };

    //   
    fetchStatus();
    
    //   (     )
    if (!pollingRef.current) {
      console.log(`[${version}]     (${pollingInterval}ms )`);
      pollingRef.current = setInterval(fetchStatus, pollingInterval);
    }
  };

  //  
  const stopPolling = () => {
    if (pollingRef.current) {
      console.log(`[${version}]    `);
      clearInterval(pollingRef.current);
      pollingRef.current = null; //  
    }
  };

  //       
  const adjustPollingInterval = (data) => {
    //          
    if (data.status === 'running') {
      const progress = data.percent || 0;
      
      //     
      if (progress < 10) {
        //      (8)
        setPollingInterval(8000);
      } else if (progress < 50) {
        //   (10)
        setPollingInterval(10000);
      } else if (progress < 80) {
        //  (15)
        setPollingInterval(15000);
      } else {
        //    (20)
        setPollingInterval(20000);
      }
    }
  };

  //      
  useEffect(() => {
    if (pollingRef.current) {
      console.log(`[${version}]   : ${pollingInterval}ms`);
      clearInterval(pollingRef.current);
      pollingRef.current = setInterval(async () => {
        //              
        const now = Date.now();
        if (isFetchingRef.current || (now - lastFetchTimeRef.current < MIN_FETCH_INTERVAL)) {
          return;
        }
        
        isFetchingRef.current = true;
        lastFetchTimeRef.current = now;
        
        try {
          const data = await aiAPI.getTrainingStatus(version);
          setStatus(data);
          
          //       
          adjustPollingInterval(data);
          
          //       
          if (data.status === 'completed' || data.status === 'failed') {
            stopPolling();
          }
        } catch (err) {
          setError(err.message || '   ');
        } finally {
          isFetchingRef.current = false;
        }
      }, pollingInterval);
    }
  }, [pollingInterval, version]);

  //     
  useEffect(() => {
    return () => {
      stopPolling();
    };
  }, []);

  return {
    status,
    loading,
    error,
    startPolling,
    stopPolling
  };
};

export default useTrainingStatus;